x}.form-image[data-v-6d394949]{height:auto;width:100%}", "",{"version":3,"sources":["webpack://./resources/js/desktop/mobile/pages/SignIn.vue"],"names":[],"mappings":"AAoRA,iCAEA,sBAAA,CACA,WAAA,CAEA,aAAA,CACA,cAAA,CAFA,sBAAA,CAHA,iBAAA,CAMA,UACA,CACA,qCACA,uBAAA,CACA,qBAEA,CACA,+BACA,wBAAA,CACA,kBAAA,CAGA,aAAA,CACA,+BAAA,CAHA,cAAA,CAIA,yBAAA,CAHA,eAAA,CAKA,mBAAA,CADA,gBAEA,CACA,qCAEA,yBAAA,CADA,sBAEA,CACA,6BAEA,yCAAA,CACA,mCAAA,CAFA,cAGA,CACA,6BAMA,gEAAA,CALA,eAAA,CAGA,cAIA,CACA,0BACA,cACA,CACA,+BAEA,cAAA,CADA,iBAEA,CACA,oCAWA,kBAAA,CAHA,eAAA,CANA,QAAA,CAQA,YAAA,CAEA,sBAAA,CATA,MAAA,CAMA,eAAA,CAIA,0CAAA,CAPA,iBAAA,CACA,kBAAA,CANA,cAAA,CAGA,OAAA,CACA,UASA,CACA,yCACA,kBAAA,CAMA,QAAA,CACA,kBAAA,CANA,UAAA,CAGA,qBAAA,CADA,cAAA,CAEA,eAAA,CAGA,YAAA,CANA,wBAOA,CACA,+CAEA,yBAAA,CADA,sBAEA,CACA,0BAKA,aAAA,CACA,+BAAA,CACA,cAAA,CAJA,MAAA,CAFA,iBAAA,CAGA,QAAA,CAFA,SAMA,CACA,+BACA,cACA,CACA,0BAKA,UAAA,CACA,+BAAA,CAEA,cAAA,CADA,eAAA,CAJA,SAAA,CAFA,iBAAA,CAGA,QAAA,CAFA,SAOA,CACA,+BACA,wBACA,CACA,+BAIA,WAAA,CAFA,SAAA,CADA,OAAA,CAEA,WAEA,CACA,6BAIA,WAAA,CAFA,UAAA,CAGA,SAAA,CAJA,QAAA,CAEA,WAGA,CACA,6BAGA,WAAA,CAFA,UAGA","sourcesContent":["<template>\n    <div class=\"start-form mt-1 pt-0\" style=\"min-height: calc(100vh - 3.5rem); height: 100%\">\n        <ValidationObserver v-slot=\"{ invalid }\" style=\"height: 100%; width: 100%\">\n            <div class=\"section text-center pt-1\">\n                <img class=\"form-image m-auto\" src=\"/images/family-illustration.png\" style=\"width: 90%; height: 90%\"/>\n            </div>\n            <div class=\"section\">\n                <div class=\"row m-auto justify-content-center align-items-center\" style=\"width: 100%; height: 100%;\">\n                    <div class=\"col-12 col-md-8 col-sm-12\">\n                        <div class=\"row align-items-center justify-content-center mx-auto mb-3 travel-card-title-row\" style=\"height:100px;width:100%;\">\n                            <div class=\"col-md-7 p-0\">\n                                <div class=\"row mr-auto ml-0 my-auto travel-card-title\" style=\"position:relative;height:50px;width:250px;\">\n                                    <h1 class=\"title-1\"><span>Д</span>обро пожаловать</h1>\n                                    <img src=\"/images/blue-brush.svg\" class=\"blue-brush\" style=\"position: absolute;\"/>\n                                </div>\n                            </div>\n                            <div class=\"col-md-7 p-0\">\n                                <div class=\"row ml-auto mr-0 my-auto travel-card-title\" style=\"position:relative;height:50px;width:280px;\">\n                                    <h1 class=\"title-2\">в <span>TravelClub Family</span></h1>\n                                    <img src=\"/images/orange-brush.svg\" class=\"orange-brush\" style=\"position: absolute;\"/>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n            </div>\n            <div class=\"section mb-2\">\n<!--                <div class=\"row m-auto justify-content-center align-items-center\" style=\"width: 100%; height: 100%;\">-->\n<!--                    <p class=\"validate-error\">{{wrongCredentials}}</p>-->\n<!--                </div>-->\n                <div class=\"row justify-content-center mx-auto mt-auto mb-3\" style=\"width: 100%;height: 100%;\">\n                    <div class=\"col-12\">\n                        <div class=\"row align-items-center m-auto\" style=\"width: 100%;\">\n                            <h4 class=\"travel-input-title\">Email</h4>\n                            <ValidationProvider name=\"email\" rules=\"required|email\" v-slot=\"{ errors }\" style=\"width: 100%;text-align: center;\">\n                                <div class=\"row align-items-center justify-content-center m-auto\" style=\"width: 100%;height: 100%;\">\n                                    <input type=\"text\" class=\"travel-input form-control\" v-model=\"email\" placeholder=\"Email\">\n                                </div>\n                                <span class=\"validate-error\">{{ errors[0] }}</span>\n                            </ValidationProvider>\n                        </div>\n                    </div>\n                    <div class=\"col-12 mt-3\">\n                        <div class=\"row align-items-center m-auto\" style=\"width: 100%;\">\n                            <h4 class=\"travel-input-title\">Пароль</h4>\n                            <ValidationProvider name=\"password\" rules=\"required\" v-slot=\"{ errors }\" style=\"width:100%; text-align: center;\">\n                                <div class=\"row align-items-center justify-content-center m-auto\" style=\"width: 100%;\">\n                                    <input\n                                        :type=\"passwordFieldType\"\n                                        class=\"travel-input form-control\"\n                                        v-model=\"password\"\n                                        placeholder=\"Пароль\"\n                                    >\n                                    <button v-if=\"passwordFieldType === 'password'\" type=\"password\" class=\"btn-visibility icon icon-eye\"  @click=\"switchVisibility\"></button>\n                                    <button v-else type=\"password\" class=\"btn-visibility icon icon-eye-slash\" @click=\"switchVisibility\"></button>\n                                </div>\n                                <span class=\"validate-error\">{{ errors[0] }}</span>\n                            </ValidationProvider>\n                        </div>\n                    </div>\n                </div>\n            </div>\n            <p class=\"text-center mb-1 mx-auto\" style=\"font-style: normal;font-size: 17px;\">\n                Нет аккаунта? <router-link to=\"/signup\" style=\"color: #f8a105\">Зарегистрироваться</router-link>\n            </p>\n            <p class=\"text-center mx-auto\" style=\"font-style: normal;font-size: 17px;margin-bottom: 1.9rem\">\n                Забыли пароль? <router-link to=\"/reset-password\" style=\"color: #f8a105\">Сменить пароль</router-link>\n            </p>\n            <div class=\"form-button-group\">\n                <button type=\"submit\" class=\"btn btn-block btn-lg\" :disabled=\"invalid\" @click=\"signin\">Войти</button>\n            </div>\n        </ValidationObserver>\n    </div>\n</template>\n\n<script>\n    export default {\n        name: \"SignIn\",\n        data: () => ({\n            email:'',\n            password:'',\n            wrongCredentials:'',\n            passwordFieldType: 'password'\n        }),\n        computed: {\n            isLoggedIn: function () {\n                return this.$store.getters.isLoggedIn\n            },\n            isAdmin: function () {\n                return this.$store.getters.isAdmin\n            },\n            isManager: function () {\n                return this.$store.getters.isManager\n            },\n        },\n        mounted() {\n            if(this.isLoggedIn)\n            {\n                this.$router.push('/dashboard')\n            }\n        },\n        methods: {\n            switchVisibility() {\n                this.passwordFieldType = this.passwordFieldType === 'password' ? 'text' : 'password'\n            },\n            async signin() {\n                this.wrongCredentials = '';\n                if(this.email !=''){\n                    let data = {\n                        email: this.email,\n                        password: this.password,\n                    }\n                    await this.$store.dispatch('login', data)\n                        .then(resp => {\n                            if(resp.data.status == 'success') {\n                                this.$router.push('/dashboard')\n                            }\n                            else {\n                                this.wrongCredentials = 'Неверные учетные данные'\n                                this.sendMessage(this.wrongCredentials);\n                            }\n                        })\n                        .catch(err => {\n                            if (err.response != undefined) {\n                                if (err.response.data.error)\n                                {\n                                    this.wrongCredentials = 'Неверные учетные данные'\n                                    this.sendMessage(this.wrongCredentials);\n                                }\n                            }\n                            else {\n                                this.wrongCredentials = 'Произошла ошибка, попробуйте снова'\n                                this.sendMessage(this.wrongCredentials);\n                            }\n\n                        })\n                }\n            },\n            sendMessage(message) {\n                this.$notify({\n                    group: 'info',\n                    type: 'error',\n                    title: 'Сообщение от TravelClub',\n                    text: message\n                });\n            },\n        }\n    }\n</script>\n\n<style scoped>\n    .btn-visibility{\n        position: absolute;\n        background: transparent;\n        border: none;\n        outline: none!important;\n        color: #063065;\n        font-size: 20px;\n        right: 30px;\n    }\n    .travel-input-title {\n        color: #062348 !important;\n        font-family: \"Bello Pro\";\n        /*font-weight: 700;*/\n    }\n    .travel-input {\n        border: 1px solid #f08b23;\n        border-radius: 50px;\n        font-size: 1rem;\n        min-height: 40px;\n        color: #062348;\n        font-family: \"Open Sans\" !important;\n        font-weight: 900 !important;\n        padding-top: 23px;\n        padding-bottom: 23px;\n    }\n    .travel-input:focus{\n        outline: none!important;\n        box-shadow: none !important;\n    }\n    #appCapsule {\n        padding: 56px 0;\n        margin-bottom: env(safe-area-inset-bottom);\n        margin-top: env(safe-area-inset-top);\n    }\n    .start-form {\n        max-width: 500px;\n        margin: auto;\n        margin-top: auto;\n        padding: 56px 0;\n        margin-bottom: env(safe-area-inset-bottom);\n        margin-top: env(safe-area-inset-top);\n        /*text-align: center;*/\n    }\n    .section {\n        padding: 0 16px;\n    }\n    .start-form p {\n        font-style: italic;\n        font-size: 12px;\n    }\n    .form-button-group {\n        position: fixed;\n        bottom: 0;\n        left: 0;\n        right: 0;\n        width: 100%;\n        padding-left: 16px;\n        padding-right: 16px;\n        background: #FFF;\n        min-height: 84px;\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        padding-bottom: env(safe-area-inset-bottom);\n    }\n    .form-button-group .btn {\n        background: #0f213d;\n        color: white;\n        text-transform: uppercase;\n        font-size: 24px;\n        font-family: \"Open Sans\";\n        font-weight: 900;\n        border: 0;\n        border-radius: 50px;\n        outline: none;\n    }\n    .form-button-group .btn:focus {\n        outline: none !important;\n        box-shadow: none !important;\n    }\n    .title-1 {\n        position: absolute;\n        z-index: 1;\n        left: 0px;\n        top: 10px;\n        color:#062348;\n        font-family: 'Bello Pro' !important;\n        font-size: 30px;\n    }\n    .title-1 span {\n        font-size: 40px;\n    }\n    .title-2 {\n        position: absolute;\n        z-index: 1;\n        left: 42px;\n        top: 22px;\n        color: white;\n        font-family: 'Open Sans' !important;\n        font-weight: 900;\n        font-size: 20px;\n    }\n    .title-2 span {\n        text-transform: uppercase;\n    }\n    .orange-brush {\n        top: 2px;\n        left: 10px;\n        width: 283px;\n        height: 60px;\n    }\n    .blue-brush{\n        top: 10px;\n        left: -55px;\n        width: 330px;\n        height: 50px;\n        opacity: 1;\n    }\n    .form-image {\n        width: 100%;\n        /*max-width: 200px;*/\n        height: auto;\n    }\n</style>\n"],"sourceRoot":""}]);
